.panel{
    position: absolute;
    background-color: lightgoldenrodyellow;
    border: 8px solid burlywood;
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    z-index: 1;
}

.app-container{
    width: 800px;
    height: 800px;
    position: absolute;
    left: 50%;
    top: 10%;
    margin-left: -400px;
}

.header{
    left: 50%;
    top:90px;
    height: 80px;
    width: 300px;
    margin-left: -120px;
}

.header-text{
    font-family: sans-serif;
    font-size: 30px;
    font-weight: bold;
    width: 200px;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.footer{
    left:50%;
    top: 750px;
    height: 80px;
    width: 300px;
    margin-left: -120px;
    display: grid;
    grid-template-columns:  1fr  1fr;
}

.footer button{
    margin-left: 5px;
    margin-top: 15px;
    width: 140px;
    height: 50px;
    background-color: lightblue;
    font-size: 20px;
    -webkit-box-shadow: 3px 5px 10px 2px rgba( 0,0,0,0.9);
    box-shadow: 3px 5px 10px 2px rgba( 0,0,0,0.9);
    border: 5px solid chocolate;
    font-weight: bold;
}

.footer button:hover{
    background-color: coral;
}

.gameBoard{
    display:grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    padding: 20px;
    width: 500px;
    position: absolute;
    left: 50%;
    margin-left: -250px;
    top: 20%;
    background-color: bisque;
    border: 8px solid burlywood;
    border-radius: 8%;
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);

}

.gameCircle{
    width: 100px;
    height: 100px; 
    margin: 10px;
    border-radius: 50%;
    border: 5px solid black;
}

.player_0{
    background-color:  white;
}
.player_1{
    background-color:  red;
}
.player_2{
    background-color: blue;
}
